<template>
  <div class="honorSystem mt72">
    <div class=" bf nav">
      <el-menu :default-active="$route.path" class="el-menu-demo"  active-text-color="#f25c29"
          background-color="#fff" mode="horizontal" router @select="handleSelect">
        <el-menu-item index="/studentCommon/honorSystem">征途</el-menu-item>
        <el-menu-item index="/studentCommon/honorSystem/challenge">挑战自己</el-menu-item>
        <el-menu-item index="/studentCommon/honorSystem/honour">荣耀</el-menu-item>
      </el-menu>
    </div>
    <router-view/>
  </div>
</template>

<script>

  export default {
    name: "activityCenter",
    data(){
      return{
        activeIndex: '1',
      }
    },
    created(){
      this.$store.state.stuadminnav = "4"; //头部导航
    },
    methods: {
      handleSelect(key, keyPath) {
        // console.log(key, keyPath);
        if(key==1){
          this.$router.push({
            path:'/studentCommon/honorSystem'
          })
        }else if(key==2){
          this.$router.push({
            path:'/studentCommon/honorSystem/challenge'
          })
        }else if(key==3){
          this.$router.push({
            path:'/studentCommon/honorSystem/honour'
          })
        }
        this.$store.state.stuhonor=key;
      },
      getNavType(){
        this.activeIndex=this.$store.state.stuhonor;
        //store.state.adminleftnavnum里值变化的时候，设置navselected
      }
    },
    watch: {
      // 监测store.state
      '$store.state.stuhonor': 'getNavType'
    }

  }
</script>

<style scoped lang="less">
  .honorSystem{
    .nav{
      margin-bottom: 20px;
    }
  }
</style>



